.header{
    width: 100%;
    height: 100px;
    line-height: 100px;
}

.logo{
    text-align: center;
}

.logo img{
    width: calc(100% - 60%);
    object-fit: contain;
    margin-top: -40px;
}

.logo h5{
    font-weight: 600;
    font-size: 1rem;
    color: #212245;
    margin-top: -40px;  
    /* margin-bottom: 0; */
}
.menu a{
    font-weight: 600;
    font-size: 1rem;
    color: #212245;
    transition: 0.3s;
}

.menu a:hover{
    color: #df2020;
}

.active__menu{
     color: #df2020 !important;
}

.cart__icon i, .ucer_icon i, .mobile__menu i{
    font-size: 1.3rem;
    counter-reset: #212245;
    cursor: pointer;
}

.cart__icon{
    position: relative;
}

.cart__badge{
    position: absolute;
    top: 15px;
    right: -10px;
    background-color: #df2020;
    color: #fff;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    font-size: 0.8rem;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
}

.mobile__menu{
    font-weight: 600;
    display: none;
}

.header__shrink{
    position: sticky;
    top: 0;
    left: 0;
    background-color: #fff;
    box-shadow: 5px 5px 15px -5px #fde4e4;
    transition: 0.3s;
    z-index: 15;
}


@media only screen and (max-width:992px) {
    .logo img{
        width: 30%;
    }

    .logo h5{
        font-size: 0.8rem;
        margin-top: -10px;
    }
    .navigation{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.468) ;
        z-index: 99;
        display: none;
    }

    .menu{
        position: absolute;
        top: 0;
        right: 0;
        width: 250px;
        height: 100%;
        background-color: #fff;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        z-index: 100;
        line-height: 20px;
    }

    .menu a{
        font-size: 0.9rem;
    }

    .mobile__menu{
        display: block;
    }

   .cart__icon i, .ucer_icon i, .mobile__menu i{
    font-size: 1rem;
   }

   .show__menu{
        display: block;
   }
}

@media only screen and (max-width: 768px) {
        .header{
            width: 100%;
            height: 50px;
            line-height: 50px;
        }

        .logo h5{
            margin-top:0;
        }
}
