@import url(https://fonts.googleapis.com/css2?family=RocknRoll+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Istok+Web&display=swap);
.category__item{
    background-color: #fde4e4;
    padding: 20px 15px;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.5s;
}

.category__item:hover{
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
}
.product__item{
    border: 1px solid #fde4e4;
    padding: 10px;
    text-align: center;
    cursor: pointer;
}

.product__img{
    margin-bottom: 20px;
    transition: 0.3s;
}

.product__img:hover{
    -webkit-transform:scale(1.2) ;
            transform:scale(1.2) ;

}

.product__content h5 a{
    color: #212245;
}

.product__content h5{
    margin-bottom: 30px;
}

.product__price{
    font-weight: 600;
    font-size: 1.2rem;
    color: #df2020;
}

.addToCart_btn{
    border: none;
    outline: none;
    background-color: #df2020;
    color: white;
    padding: 7px 25px;
    border-radius: 5px;
    font-size: 0.9rem;
}


.hero__content{
    padding-top: 70px;
}
.hero__title{
    font-size: 2.5rem;
    line-height: 55px;
}

.hero__title span{
    color: #df2020;
}

.hero__content p{
    font-size: 1rem;
    line-height: 30px;
    color: #777;
}

.hero__btns button{
    border: none;
    outline: none;
    padding: 7px 20px;
}

.order__btn{
    margin-right: 50px;
    background-color: #df2020;
    color: white;
    border-radius: 5px;
}

.hero__btns .all-food__btn{
    border: 1px solid #df2020;
    background-color:white;
    color:#df2020;
    border-radius: 5px;
}

.hero__icon i{
    width: 30px;
    height: 30px;
    background-color: #df2020;
    border-radius: 50%;
    padding: 5px;
    color: white;
    margin-right: 7px;
}

.service p{
    color: black;
    font-weight: 700;
    margin:30px;
}

.hero__img img{
    width: 700px;
    height: 700px;

}

.serve .serve__title span{
    color: #df2020;
}

.serve .title{
    color: #777;
}

.food__category{
    background-color: #df2020;
    text-align: center;
    padding: 20px 0;
    border-radius: 5px;
}

.food__category button{
    border: none;
    outline: none;
    padding: 7px 20px;
    background: transparent;
    color: white
    ;
}

.food__category button img{
    width: 20px;
    height: 20px;
}
.food__category .food__category__active{
    background-color: white;
    color: #df2020;
    border-radius: 10px;
}

.common__section{
    background:  url(../../static/media/banner-02.1d3252d3.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 80px 0; 
}

.common__section h2{
    color: white;
}
.search__widget{
    padding: 7px;
    border: 1px solid #fde4e4;
    border-radius: 5px;
}

.search__widget input{
    border: none;
    outline: none;
}

.search__widget i{
    cursor: pointer;
}

.cart__item-info img{   
    width: 35px;
    height: 35px;

}

.cart__product-title{
    font-style: 0.7rem;
    font-weight: 600;

}

.cart__product-price{
    font-style: 0.7rem;
}

.cart__product-price span{
    font-style: 0.9rem;
    font-weight: 600;
    color: #df2020;
}

.btn-in-de{
    background-color: #fde4e4;
    padding: 2px 3px;
    border-radius: 5px;
}

.delete__btn{
    
}
.cart__container{
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.639);
    z-index: 999999;
}

.cart{
    position: absolute;
    top: 0;
    right: 0;
    width: 400px;
    height: 100%;
    background: white;
    z-index: 999999;
}

.cart__close{
    width: 100%;
    height: 50px;
    padding: 10px;
}

.cart__close i{
    font-style: 1.2rem;
    background: #000;
    padding: 5px;
    border-radius: 50%;
    color: white;
}

.cart__bottom{
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 10px 20px;
    width: 100%;
    height: 80px;
    background-color: #df2020;
}

.cart__bottom h6{
    color: white;
}
.cart__bottom button{
    border: none;
    outline: none;
    padding: 5px 10px;
    border-radius: 5px;
}

.cart__item-list{
    height: calc(100vh -130px);
    overflow-y: scroll;
}


.footer {
    padding: 50px 0px;
    background-color: #fde4e4;
}

.footer .footer__logo img{
    width: 20%;
    margin-bottom: 5px;
}

.footer .footer__logo h5{
    font-size: 1rem;
    font-weight: 600;
    margin-bottom: 15px;
}
.footer .footer__logo p{
    font-size: 0.9rem;
    color: #333;
    line-height: 28px;
}

.delivery__time-item{
    background-color: transparent !important;
}

.delivery__time-item span{
    font-weight: 600;
    font-size: 1rem;
    color: #212245;
    background: transparent;
}

.footer .footer__title{
    font-size: 1.1rem;
    color: #212245;
    font-weight: 600;
}

.footer .newSletter{
    padding: 7px 25px;
    border: 1px solid #212245;
    border-radius: 5px;

}

.footer .newSletter input{
    background: transparent;
    border: none;
    outline: none;
}

.header{
    width: 100%;
    height: 100px;
    line-height: 100px;
}

.logo{
    text-align: center;
}

.logo img{
    width: calc(100% - 60%);
    object-fit: contain;
    margin-top: -40px;
}

.logo h5{
    font-weight: 600;
    font-size: 1rem;
    color: #212245;
    margin-top: -40px;  
    /* margin-bottom: 0; */
}
.menu a{
    font-weight: 600;
    font-size: 1rem;
    color: #212245;
    transition: 0.3s;
}

.menu a:hover{
    color: #df2020;
}

.active__menu{
     color: #df2020 !important;
}

.cart__icon i, .ucer_icon i, .mobile__menu i{
    font-size: 1.3rem;
    counter-reset: #212245;
    cursor: pointer;
}

.cart__icon{
    position: relative;
}

.cart__badge{
    position: absolute;
    top: 15px;
    right: -10px;
    background-color: #df2020;
    color: #fff;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    font-size: 0.8rem;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
}

.mobile__menu{
    font-weight: 600;
    display: none;
}

.header__shrink{
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    background-color: #fff;
    box-shadow: 5px 5px 15px -5px #fde4e4;
    transition: 0.3s;
    z-index: 15;
}


@media only screen and (max-width:992px) {
    .logo img{
        width: 30%;
    }

    .logo h5{
        font-size: 0.8rem;
        margin-top: -10px;
    }
    .navigation{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.468) ;
        z-index: 99;
        display: none;
    }

    .menu{
        position: absolute;
        top: 0;
        right: 0;
        width: 250px;
        height: 100%;
        background-color: #fff;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        z-index: 100;
        line-height: 20px;
    }

    .menu a{
        font-size: 0.9rem;
    }

    .mobile__menu{
        display: block;
    }

   .cart__icon i, .ucer_icon i, .mobile__menu i{
    font-size: 1rem;
   }

   .show__menu{
        display: block;
   }
}

@media only screen and (max-width: 768px) {
        .header{
            width: 100%;
            height: 50px;
            line-height: 50px;
        }

        .logo h5{
            margin-top:0;
        }
}

/* google fonts */

/* reset default style */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* base style */

body {
  background: #fcfcfc;
  font-family: "Istok Web", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "RocknRoll One", sans-serif !important;
}

a {
  text-decoration: none;
  color: unset;
}

h1,
h2 {
  font-size: 2.1rem;
  font-weight: 600;
  color: #212245;
}

section {
  padding: 60px 0px;
}

